<script>
import { onMount, getContext } from "svelte";

export let tipo = "insertar";
export let coleccion = "articulos";
export let documento = {};

let URL = getContext ("URL");

let clases = "";
let handler =  () => {};
let url = "";

function insertar () {  console.log ("insertar"); }

function modificar () { console.log ("modificar");  }

function eliminar () { 
    fetch (url + documento._id, { method: 'DELETE' })
    .then (response => response.json())
    .then (data => console.log (data));
}

onMount (() => {
    switch (tipo) {
        case "insertar":
            clases="insertar";
            handler = insertar;
            break;
        case "modificar":
            clases="modificar";
            handler = modificar;
            break;
        case "eliminar":
            clases="eliminar";
            handler = eliminar;
            break;
        default:
    }

    switch (coleccion) {
        case "articulos":
            url = URL.articulos;
            break;
        case "clientes":
            url = URL.clientes;
            break;
    }

});
    



</script>



<button class={clases}  on:click={handler} />


<style>
    .insertar { background-color: lightgreen;   }
    .insertar::after  { content: "Insertar"; }

    .modificar { background-color: lightblue;   }
    .modificar::after  { content: "Modificar"; }

    .eliminar { background-color: lightcoral;   }
    .eliminar::after  { content: "Eliminar"; }
</style>